import React from 'react';
import { Link } from 'react-router-dom';
import './Header.css';

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            active: false
        }
        
        this.handleToggle = this.handleToggle.bind(this);
    }

    handleToggle() {
        let checkbox = document.getElementById("menu__toggle");
        checkbox.checked = !checkbox.checked;
    }

    render() {
           return (
        <div id="Header">
            <div className = "hamburger-menu">
                <Link className="logo_wrapper" to='/'>
                    <div className = "logo_995px">
                        <div className="logo_img"></div>
                        <h1 className="logo_text">The Lords Of The React </h1>
                    </div>
                </Link>
                <input id="menu__toggle" type="checkbox" />
                <label className="menu__btn" htmlFor="menu__toggle">
                <span></span>
                </label>
                
                <div className="menu__box">
                        <Link className="header-item-wrapper" to="/movies" onClick={this.handleToggle}> 
                                <div   className="menu__item">Movies</div>
                        </Link>
                        
                        <Link className="header-item-wrapper" to='/books' onClick={this.handleToggle}>
                            <div  className="menu__item">Books</div>
                        </Link>
                        <Link className="header-item-wrapper" to="/characters" onClick={this.handleToggle}>
                            <div  className="menu__item">Characters</div>
                        </Link>
                </div>
            </div>
        </div>
    );
    }

}


export default Header;

